<template><section><h2>Checkbox 多选框</h2>
<p>多选框。</p>
<h2>何时使用</h2>
<ul>
<li>在一组可选项中进行多项选择时；</li>
<li>单独使用可以表示两种状态之间的切换，和 <code>switch</code> 类似。区别在于切换 <code>switch</code> 会直接触发状态改变，而 <code>checkbox</code> 一般用于状态标记，需要和提交操作配合。</li>
</ul>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<h3>Checkbox</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>checked</td>
<td>指定当前是否选中</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>defaultChecked</td>
<td>初始是否选中</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>onChange</td>
<td>变化时回调函数</td>
<td>Function(e:Event)</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>Checkbox Group</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>defaultValue</td>
<td>默认选中的选项</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>value</td>
<td>指定选中的选项</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>options</td>
<td>指定可选项</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>onChange</td>
<td>变化时回调函数</td>
<td>Function(checkedValue)</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import Demo from 'pages/checkbox/demo'

export default {
  components: {
    Demo
  }
}
</script>


