<template><section><h2>Form 表单</h2>
<p>具有数据收集、校验和提交功能的表单，包含复选框、单选框、输入框、下拉选择框等元素。</p>
<h2>表单</h2>
<p>我们为 <code>form</code> 提供了以下两种排列方式：</p>
<ul>
<li>水平排列：可以实现 <code>label</code> 标签和表单控件的水平排列；</li>
<li>行内排列：使其表现为 <code>inline-block</code> 级别的控件。</li>
</ul>
<h2>表单域</h2>
<p>表单一定会包含表单域，表单域可以是输入控件，标准表单域，标签，下拉菜单，文本域等。</p>
<p>这里我们封装了表单域 <code>&lt;Form.Item /&gt;</code> 。</p>
<pre><code class="language-jsx">&lt;Form.Item {...props}&gt;
  {children}
&lt;<span class="hljs-regexp">/Form.Item&gt;
</span></code></pre>
<blockquote>
<p>注：标准表单中一律使用大号控件。</p>
</blockquote>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<h3>Form</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>form</td>
<td>经 <code>Form.create()</code> 包装过的组件会自带 <code>this.props.form</code> 属性，直接传给 Form 即可。1.7.0 之后无需设置</td>
<td>object</td>
<td>无</td>
</tr>
<tr>
<td>vertical</td>
<td>垂直排列布局</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>horizontal</td>
<td>水平排列布局</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>inline</td>
<td>行内排列布局</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>onSubmit</td>
<td>数据验证成功后回调事件</td>
<td>Function(e:Event)</td>
<td></td>
</tr>
<tr>
<td>prefixCls</td>
<td>样式类名，默认为 ant-form，通常您不需要设置</td>
<td>string</td>
<td>'ant-form'</td>
</tr>
</tbody>
</table>
<h3>Form.create(options)</h3>
<p>使用方式如下：</p>
<pre><code class="language-jsx"><span class="hljs-class"><span class="hljs-keyword">class</span> <span class="hljs-title">CustomizedForm</span> <span class="hljs-keyword">extends</span> <span class="hljs-title">React</span>.<span class="hljs-title">Component</span> </span>{}

CustomizedForm = Form.create({})(CustomizedForm);
</code></pre>
<p><code>options</code> 的配置项如下。</p>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
</tr>
</thead>
<tbody>
<tr>
<td>onFieldsChange</td>
<td>当 <code>Form.Item</code> 子节点的值发生改变时触发，可以把对应的值转存到 Redux store</td>
<td>Function(props, fields)</td>
</tr>
<tr>
<td>mapPropsToFields</td>
<td>把 props 转为对应的值，可用于把 Redux store 中的值读出</td>
<td>Function(props): Object{ fieldName: Object{ value } }</td>
</tr>
</tbody>
</table>
<p>经过 <code>Form.create</code> 包装的组件将会自带 <code>this.props.form</code> 属性，<code>this.props.form</code> 提供的 API 如下：</p>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
</tr>
</thead>
<tbody>
<tr>
<td>getFieldsValue</td>
<td>获取一组输入控件的值，如不传入参数，则获取全部组件的值</td>
<td>Function([fieldNames: string[]])</td>
</tr>
<tr>
<td>getFieldValue</td>
<td>获取一个输入控件的值</td>
<td>Function(fieldName: string)</td>
</tr>
<tr>
<td>setFieldsValue</td>
<td>设置一组输入控件的值</td>
<td>Function(obj: object)</td>
</tr>
<tr>
<td>setFields</td>
<td>设置一组输入控件的值与 Error</td>
<td>Function(obj: object)</td>
</tr>
<tr>
<td>validateFields</td>
<td>校验并获取一组输入域的值与 Error</td>
<td>Function([fieldNames: string[]], [options: object], callback: Function(errors, values))</td>
</tr>
<tr>
<td>validateFieldsAndScroll</td>
<td>与 <code>validateFields</code> 相似，但校验完后，如果校验不通过的菜单域不在可见范围内，则自动滚动进可见范围</td>
<td>参考 <code>validateFields</code></td>
</tr>
<tr>
<td>getFieldError</td>
<td>获取某个输入控件的 Error</td>
<td>Function(name)</td>
</tr>
<tr>
<td>isFieldValidating</td>
<td>判断一个输入控件是否在校验状态</td>
<td>Function(name)</td>
</tr>
<tr>
<td>resetFields</td>
<td>重置一组输入控件的值与状态，如不传入参数，则重置所有组件</td>
<td>Function([names: string[]])</td>
</tr>
<tr>
<td>getFieldProps</td>
<td>用于和表单进行双向绑定，详见下方描述</td>
<td></td>
</tr>
</tbody>
</table>
<h3>this.props.form.getFieldProps(id, options)</h3>
<h4>特别注意</h4>
<p>如果使用的是 <code>react@&lt;15.3.0</code>，则 <code>getFieldProps</code> 调用不能位于纯函数组件中: https://github.com/facebook/react/pull/6534</p>
<p><code>getFieldProps</code> 返回的属性包括 <code>id</code>、<code>value</code>（或你设置的其它 <code>valuePropName</code>）、<code>ref</code>、<code>onChange</code>（或者你设置的其它 <code>trigger</code> <code>validateTrigger</code>），<strong>所以不应再设置同样的属性</strong>，以免冲突。如果对其返回值的细节有兴趣，可以 <code>console.log</code> 出来查看。</p>
<blockquote>
<p>在表单中 <code>defaultValue</code> 也不应该被设置，请使用下面的 <code>initialValue</code>。</p>
</blockquote>
<h4>getFieldProps options</h4>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>options.id</td>
<td>必填输入控件唯一标志</td>
<td>string</td>
<td></td>
</tr>
<tr>
<td>options.valuePropName</td>
<td>子节点的值的属性，如 Switch 的是 'checked'</td>
<td>string</td>
<td>'value'</td>
</tr>
<tr>
<td>options.initialValue</td>
<td>子节点的初始值，类型、可选值均由子节点决定</td>
<td></td>
<td></td>
</tr>
<tr>
<td>options.trigger</td>
<td>收集子节点的值的时机</td>
<td>string</td>
<td>'onChange'</td>
</tr>
<tr>
<td>options.getValueFromEvent</td>
<td>可以把 onChange 的参数转化为控件的值，例如 DatePicker 可设为：<code>(date, dateString) =&gt; dateString</code></td>
<td>function(..args)</td>
<td><a href="https://github.com/react-component/form#optiongetvaluefromevent">reference</a></td>
</tr>
<tr>
<td>options.validateTrigger</td>
<td>校验子节点值的时机</td>
<td>string</td>
<td>'onChange'</td>
</tr>
<tr>
<td>options.rules</td>
<td>校验规则，参见 <a href="https://github.com/yiminghe/async-validator">async-validator</a></td>
<td>array</td>
<td></td>
</tr>
<tr>
<td>options.onXXX</td>
<td>由于 <code>getFieldProps</code> 会占用 <code>onChange</code> 等事件（即你所设置的 <code>trigger</code> <code>validateTrigger</code>），所以如果仍需绑定事件，请在 <code>options</code> 内设置</td>
<td>function</td>
<td>无</td>
</tr>
<tr>
<td>options.exclusive</td>
<td>是否和其他控件互斥，特别用于 Radio 单选控件</td>
<td>boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>Form.Item</h3>
<blockquote>
<p>一个 Form.Item 建议只放一个 child，有多个 child 时，<code>help</code> <code>required</code> <code>validateStatus</code> 无法自动生成。</p>
</blockquote>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>label 标签的文本</td>
<td>string</td>
<td></td>
<td></td>
</tr>
<tr>
<td>labelCol</td>
<td>label 标签布局，通 <code>&lt;Col&gt;</code> 组件，设置 <code>span</code> <code>offset</code> 值，如 <code>{span: 3, offset: 12}</code></td>
<td>object</td>
<td></td>
<td></td>
</tr>
<tr>
<td>wrapperCol</td>
<td>需要为输入控件设置布局样式时，使用该属性，用法同 labelCol</td>
<td>object</td>
<td></td>
<td></td>
</tr>
<tr>
<td>help</td>
<td>提示信息，如不设置，则会根据校验规则自动生成</td>
<td>string</td>
<td></td>
<td></td>
</tr>
<tr>
<td>extra</td>
<td>额外的提示信息，和 help 类似，当需要错误信息和提示文案同时出现时，可以使用这个。</td>
<td>string</td>
<td></td>
<td></td>
</tr>
<tr>
<td>required</td>
<td>是否必填，如不设置，则会根据校验规则自动生成</td>
<td>bool</td>
<td></td>
<td>false</td>
</tr>
<tr>
<td>validateStatus</td>
<td>校验状态，如不设置，则会根据校验规则自动生成</td>
<td>string</td>
<td>'success' 'warning' 'error' 'validating'</td>
<td></td>
</tr>
<tr>
<td>hasFeedback</td>
<td>配合 validateStatus 属性使用，展示校验状态图标，建议只配合 Input 组件使用</td>
<td>bool</td>
<td></td>
<td>false</td>
</tr>
<tr>
<td>prefixCls</td>
<td>样式类名，默认为 ant-form，通常您不需要设置</td>
<td>string</td>
<td></td>
<td>'ant-form'</td>
</tr>
</tbody>
</table>

</section></template>
<style>
.code-box-demo .ant-form-horizontal {
  max-width: 540px;
}
</style>
<script>
import Demo from 'pages/form/demo'

export default {
  components: {
    Demo
  }
}
</script>


