<template><section><h2>Input 输入框</h2>
<p>通过鼠标或键盘输入内容，是最基础的表单域的包装。</p>
<h2>何时使用</h2>
<ul>
<li>需要用户输入表单域内容时。</li>
<li>提供组合型输入框，带搜索的输入框，还可以进行大小选择。</li>
</ul>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<h3>Input</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>【必须】声明 input 类型，同原生 input 标签的 type 属性。另外提供 <code>type=&quot;textarea&quot;</code>。</td>
<td>string</td>
<td></td>
<td>'text'</td>
</tr>
<tr>
<td>id</td>
<td>id</td>
<td>number 或 string</td>
<td></td>
<td></td>
</tr>
<tr>
<td>value</td>
<td>value 值</td>
<td>any</td>
<td></td>
<td></td>
</tr>
<tr>
<td>defaultValue</td>
<td>设置初始默认值</td>
<td>any</td>
<td></td>
<td></td>
</tr>
<tr>
<td>size</td>
<td>控件大小，默认值为 default 。注：标准表单内的输入框大小限制为 large。</td>
<td>string</td>
<td>{'large','default','small'}</td>
<td>'default'</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用状态，默认为 false</td>
<td>bool</td>
<td></td>
<td>false</td>
</tr>
<tr>
<td>addonBefore</td>
<td>带标签的 input，设置前置标签</td>
<td>node</td>
<td></td>
<td></td>
</tr>
<tr>
<td>addonAfter</td>
<td>带标签的 input，设置后置标签</td>
<td>node</td>
<td></td>
<td></td>
</tr>
<tr>
<td>onPressEnter</td>
<td>按下回车的回调</td>
<td>function(e)</td>
<td></td>
<td></td>
</tr>
<tr>
<td>autosize</td>
<td>自适应内容高度，只对 <code>type=&quot;textarea&quot;</code> 有效</td>
<td>bool or object</td>
<td><code>true</code> or <code>{ minRows: 2, maxRows: 6 }</code></td>
<td>false</td>
</tr>
</tbody>
</table>
<blockquote>
<p>如果 <code>Input</code> 在 <code>Form.Item</code> 内，并且 <code>Form.Item</code> 设置了 <code>id</code> 和 <code>options</code> 属性，则 <code>value</code> <code>defaultValue</code> 和 <code>id</code> 属性会被自动设置。</p>
</blockquote>
<h4>InputGroup</h4>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>size</td>
<td><code>Input.Group</code> 中所有的 <code>Input</code> 的大小</td>
<td>string</td>
<td>{'large','default','small'}</td>
<td>'default'</td>
</tr>
</tbody>
</table>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">ant-input-group</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"string"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">ant-input</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">ant-input</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">ant-input</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">ant-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">ant-input-group</span>&gt;</span>
</code></pre>
</section></template>

<script>
import Demo from 'pages/input/demo'

export default {
  components: {
    Demo
  }
}
</script>


