<template><section><h2>Breadcrumb 面包屑</h2>
<p>显示当前页面在系统层级结构中的位置，并能向上返回。</p>
<h2>何时使用</h2>
<ul>
<li>当系统拥有超过两级以上的层级结构时；</li>
<li>当需要告知用户『你在哪里』时；</li>
<li>当需要向上导航的功能时。</li>
</ul>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">ant-breadcrumb</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">ant-breadcrumb-item</span>&gt;</span>首页<span class="hljs-tag">&lt;/<span class="hljs-name">ant-breadcrumb-item</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">ant-breadcrumb-item</span>&gt;</span>应用中心<span class="hljs-tag">&lt;/<span class="hljs-name">ant-breadcrumb-item</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">ant-breadcrumb-item</span>&gt;</span>应用列表<span class="hljs-tag">&lt;/<span class="hljs-name">ant-breadcrumb-item</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">ant-breadcrumb-item</span>&gt;</span>某应用<span class="hljs-tag">&lt;/<span class="hljs-name">ant-breadcrumb-item</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">ant-breadcrumb</span>&gt;</span>
</code></pre>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>routes</td>
<td>router 的路由栈信息</td>
<td>Array</td>
<td></td>
<td>-</td>
</tr>
<tr>
<td>params</td>
<td>路由的参数</td>
<td>Object</td>
<td></td>
<td>-</td>
</tr>
<tr>
<td>separator</td>
<td>分隔符自定义</td>
<td>String or Element</td>
<td></td>
<td>'/'</td>
</tr>
<tr>
<td>linkRender</td>
<td>自定义链接函数，和 react-router 配置使用</td>
<td>Function(href, name, paths)</td>
<td></td>
<td>-</td>
</tr>
<tr>
<td>nameRender</td>
<td>自定义文字函数，和 react-router 配置使用</td>
<td>Function(breadcrumbName, route, params)</td>
<td></td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import Demo from 'pages/breadcrumb/demo'

export default {
  components: {
    Demo
  }
}
</script>


