module.exports = "<section><h2>Form 表单</h2> <p>具有数据收集、校验和提交功能的表单，包含复选框、单选框、输入框、下拉选择框等元素。</p> <h2>表单</h2> <p>我们为 <code>form</code> 提供了以下两种排列方式：</p> <ul> <li>水平排列：可以实现 <code>label</code> 标签和表单控件的水平排列；</li> <li>行内排列：使其表现为 <code>inline-block</code> 级别的控件。</li> </ul> <h2>表单域</h2> <p>表单一定会包含表单域，表单域可以是输入控件，标准表单域，标签，下拉菜单，文本域等。</p> <p>这里我们封装了表单域 <code>&lt;Form.Item /&gt;</code> 。</p> <pre><code class=language-jsx>&lt;Form.Item {...props}&gt;\n  {children}\n&lt;<span class=hljs-regexp>/Form.Item&gt;\n</span></code></pre> <blockquote> <p>注：标准表单中一律使用大号控件。</p> </blockquote> <h2>代码演示</h2> <p><demo></demo></p> <h2>API</h2> <h3>Form</h3> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>form</td> <td>经 <code>Form.create()</code> 包装过的组件会自带 <code>this.props.form</code> 属性，直接传给 Form 即可。1.7.0 之后无需设置</td> <td>object</td> <td>无</td> </tr> <tr> <td>vertical</td> <td>垂直排列布局</td> <td>boolean</td> <td>false</td> </tr> <tr> <td>horizontal</td> <td>水平排列布局</td> <td>boolean</td> <td>false</td> </tr> <tr> <td>inline</td> <td>行内排列布局</td> <td>boolean</td> <td>false</td> </tr> <tr> <td>onSubmit</td> <td>数据验证成功后回调事件</td> <td>Function(e:Event)</td> <td></td> </tr> <tr> <td>prefixCls</td> <td>样式类名，默认为 ant-form，通常您不需要设置</td> <td>string</td> <td>'ant-form'</td> </tr> </tbody> </table> <h3>Form.create(options)</h3> <p>使用方式如下：</p> <pre><code class=language-jsx><span class=hljs-class><span class=hljs-keyword>class</span> <span class=hljs-title>CustomizedForm</span> <span class=hljs-keyword>extends</span> <span class=hljs-title>React</span>.<span class=hljs-title>Component</span> </span>{}\n\nCustomizedForm = Form.create({})(CustomizedForm);\n</code></pre> <p><code>options</code> 的配置项如下。</p> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> </tr> </thead> <tbody> <tr> <td>onFieldsChange</td> <td>当 <code>Form.Item</code> 子节点的值发生改变时触发，可以把对应的值转存到 Redux store</td> <td>Function(props, fields)</td> </tr> <tr> <td>mapPropsToFields</td> <td>把 props 转为对应的值，可用