ions</h4> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>options.id</td> <td>必填输入控件唯一标志</td> <td>string</td> <td></td> </tr> <tr> <td>options.valuePropName</td> <td>子节点的值的属性，如 Switch 的是 'checked'</td> <td>string</td> <td>'value'</td> </tr> <tr> <td>options.initialValue</td> <td>子节点的初始值，类型、可选值均由子节点决定</td> <td></td> <td></td> </tr> <tr> <td>options.trigger</td> <td>收集子节点的值的时机</td> <td>string</td> <td>'onChange'</td> </tr> <tr> <td>options.getValueFromEvent</td> <td>可以把 onChange 的参数转化为控件的值，例如 DatePicker 可设为：<code>(date, dateString) =&gt; dateString</code></td> <td>function(..args)</td> <td><a href=https://github.com/react-component/form#optiongetvaluefromevent>reference</a></td> </tr> <tr> <td>options.validateTrigger</td> <td>校验子节点值的时机</td> <td>string</td> <td>'onChange'</td> </tr> <tr> <td>options.rules</td> <td>校验规则，参见 <a href=https://github.com/yiminghe/async-validator>async-validator</a></td> <td>array</td> <td></td> </tr> <tr> <td>options.onXXX</td> <td>由于 <code>getFieldProps</code> 会占用 <code>onChange</code> 等事件（即你所设置的 <code>trigger</code> <code>validateTrigger</code>），所以如果仍需绑定事件，请在 <code>options</code> 内设置</td> <td>function</td> <td>无</td> </tr> <tr> <td>options.exclusive</td> <td>是否和其他控件互斥，特别用于 Radio 单选控件</td> <td>boolean</td> <td>false</td> </tr> </tbody> </table> <h3>Form.Item</h3> <blockquote> <p>一个 Form.Item 建议只放一个 child，有多个 child 时，<code>help</code> <code>required</code> <code>validateStatus</code> 无法自动生成。</p> </blockquote> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>可选值</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>label</td> <td>label 标签的文本</td> <td>string</td> <td></td> <td></td> </tr> <tr> <td>labelCol</td> <td>label 标签布局，通 <code>&lt;Col&gt;</code> 组件，设置 <code>span</code> <code>offset</code> 值，如 <code>{span: 3, offset: 12}</code></td> <td>object</td> <td></td> <td></td> </tr> <tr> <td>wrapperCol</td> <td>需要为输入控件设置布局样式时，使用该属性，用法同 labelCol</td> <td>object</td> <td></td> <td></td> 