<template><section><h2>Collapse 折叠面板</h2>
<p>可以折叠/展开的内容区域。</p>
<h2>何时使用</h2>
<ul>
<li>对复杂区域进行分组和隐藏，保持页面的整洁。</li>
<li><code>手风琴</code> 是一种特殊的折叠面板，只允许单个内容区域展开。</li>
</ul>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<h3>Collapse</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>activeKey</td>
<td>当前激活 tab 面板的 key</td>
<td>Array or String</td>
<td>默认无，accordion模式下默认第一个元素</td>
</tr>
<tr>
<td>defaultActiveKey</td>
<td>初始化选中面板的 key</td>
<td>String</td>
<td>无</td>
</tr>
<tr>
<td>onChange</td>
<td>切换面板的回调</td>
<td>Function</td>
<td>无</td>
</tr>
</tbody>
</table>
<h3>Collapse.Panel</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>key</td>
<td>对应 activeKey</td>
<td>String</td>
<td>无</td>
</tr>
<tr>
<td>header</td>
<td>面板头内容</td>
<td>React.Element or String</td>
<td>无</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import Demo from 'pages/collapse/demo'

export default {
  components: {
    Demo
  }
}
</script>


