<template><section><h2>Badge 徽标数</h2>
<p>图标右上角的圆形徽标数字。</p>
<h2>何时使用</h2>
<p>一般出现在通知图标或头像的右上角，用于显示需要处理的消息条数，通过醒目视觉形式吸引用户处理。</p>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<pre><code>&lt;ant-badge :count=&quot;5&quot;&gt;
  &lt;a href=&quot;#&quot; class=&quot;head-example&quot; /&gt;
&lt;/ant-badge&gt;
</code></pre>
<pre><code>&lt;ant-badge :count=&quot;5&quot; /&gt;
</code></pre>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>count</td>
<td>展示的数字，大于 overflowCount 时显示为 <code>${overflowCount}+</code>，为 0 时隐藏</td>
<td>Number</td>
<td></td>
<td></td>
</tr>
<tr>
<td>overflowCount</td>
<td>展示封顶的数字值</td>
<td>Number</td>
<td></td>
<td>99</td>
</tr>
<tr>
<td>dot</td>
<td>不展示数字，只有一个小红点</td>
<td>Boolean</td>
<td></td>
<td>false</td>
</tr>
<tr>
<td>status</td>
<td>设置 Badge 为状态点</td>
<td>Enum</td>
<td>'success'、'processing'、'default'、'error'、'warning'</td>
<td>''</td>
</tr>
<tr>
<td>text</td>
<td>在设置了 <code>status</code> 的前提下有效，设置状态点的文本</td>
<td>String</td>
<td></td>
<td>''</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import Demo from 'pages/badge/demo'

export default {
  components: {
    Demo
  }
}
</script>


