module.exports = "<section><h2>Table 表格</h2> <p>展示行列数据。</p> <h2>何时使用</h2> <ul> <li>当有大量结构化的数据需要展现时；</li> <li>当需要对数据进行排序、搜索、分页、自定义操作等复杂行为时。</li> </ul> <h2>如何使用</h2> <p>指定表格的数据源 <code>dataSource</code> 为一个数组。</p> <pre><code class=language-jsx><span class=hljs-keyword>const</span> dataSource = [{\n  key: <span class=hljs-string>'1'</span>,\n  name: <span class=hljs-string>'胡彦斌'</span>,\n  age: <span class=hljs-number>32</span>,\n  address: <span class=hljs-string>'西湖区湖底公园1号'</span>\n}, {\n  key: <span class=hljs-string>'2'</span>,\n  name: <span class=hljs-string>'胡彦祖'</span>,\n  age: <span class=hljs-number>42</span>,\n  address: <span class=hljs-string>'西湖区湖底公园1号'</span>\n}];\n\n<span class=hljs-keyword>const</span> columns = [{\n  title: <span class=hljs-string>'姓名'</span>,\n  dataIndex: <span class=hljs-string>'name'</span>,\n  key: <span class=hljs-string>'name'</span>,\n}, {\n  title: <span class=hljs-string>'年龄'</span>,\n  dataIndex: <span class=hljs-string>'age'</span>,\n  key: <span class=hljs-string>'age'</span>,\n}, {\n  title: <span class=hljs-string>'住址'</span>,\n  dataIndex: <span class=hljs-string>'address'</span>,\n  key: <span class=hljs-string>'address'</span>,\n}];\n\n<span class=xml><span class=hljs-tag>&lt;<span class=hljs-name>Table</span> <span class=hljs-attr>dataSource</span>=<span class=hljs-string>{dataSource}</span> <span class=hljs-attr>columns</span>=<span class=hljs-string>{columns}</span> /&gt;</span>\n</span></code></pre> <h2>代码演示</h2> <p><demo></demo></p> <h2>API</h2> <h3>Table</h3> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>rowSelection</td> <td>列表项是否可选择，<a href=#rowSelection>配置项</a></td> <td>Object</td> <td>null</td> </tr> <tr> <td>pagination</td> <td>分页器，配置项参考 <a href=/components/pagination>pagination</a>，设为 false 时不显示分页</td> <td>Object</td> <td></td> </tr> <tr> <td>size</td> <td>正常或迷你类型，<code>default</code> or <code>small</code></td> <td>String</td> <td>default</td> </tr> <tr> <td>dataSource</td> <td>数据数组</td> <td>Array</td> <td></td> </tr> <tr> <td>columns</td> <td>表格列的配置描述，具体项见下表</td> <td>Array</td> <td>-</td> </tr> <tr> <td>rowKey</td> <td>表格行 key 的取值，可以是字符串或一个函数</td> <td>String or Function(record, index):string</td> <td>'key'</td> </tr> <tr> <td>rowClassName</td> <td>表格行的类名</td> <td>Function(record, index):string</td> <td>-</td> </tr> <tr> <td>expandedRowRender</td> <td>额外的展开行</td> <td>Function</td> <td>-</td> </tr> <tr> <td>defaultExpandedRowKeys</td> <td>默认展开的行</td> <td>Array</td> <td>-</td> </tr> <tr> <td>expandedRowKeys</td> <td>展开的行，控制属性</td> <td>Array</td> <td>-</td> </tr> <tr> <td>onChange</td> <td>分页、排序、筛选变化时触发</td> <td>Function(pagination, filters, sorter)</td> <td></td> </tr> <tr> <td>loading</td> <td>页面是否加载中</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>locale</td> <td>默认文案设置，目前包括排序、过滤、空数据文案</td> <td>Object</td> <td>filterConfirm: '确定' <br> filterReset: '重置' <br> emptyText: '暂无数据' <br> <a href=https://github.com/ant-design/ant-design/issues/575#issuecomment-159169511>默认值</a></td> </tr> <tr> <td>indentSize</td> <td>展示树形数据时，每层缩进的宽度，以 px 为单位</td> <td>Number</td> <td>15</td> </tr> <tr> <td>onRowClick</td> <td>处理行点击事件</td> <td>Function(record, index)</td> <td>-</td> </tr> <tr> <td>bordered</td> <td>是否展示外边框和列边框</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>showHeader</td> <td>是否显示表头</td> <td>Boolean</td> <td>true</td> </tr> <tr> <td>footer</td> <td>表格尾部</td> <td>Function(currentPageData)</td> <td></td> </tr> <tr> <td>title</td> <td>表格标题</td> <td>Function(currentPageData)</td> <td></td> </tr> <tr> <td>scroll</td> <td>横向或纵向支持滚动，也可用于指定滚动区域的宽高度：<code><span>{{</span> x: true, y: 300 <span>}}</span></code></td> <td>Object</td> <td>-</td> </tr> </tbody> </table> <h3>Column</h3> <p>列描述数据对象，是 columns 中的一项。</p> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>title</td> <td>列头显示文字</td> <td>String or React.Element</td> <td>-</td> </tr> <tr> <td>key</td> <td>React 需要的 key，建议设置</td> <td>String</td> <td>-</td> </tr> <tr> <td>dataIndex</td> <td>列数据在数据项中对应的 key，支持 <code>a.b.c</code> 的嵌套写法</td> <td>String</td> <td>-</td> </tr> <tr> <td>render</td> <td>生成复杂数据的渲染函数，参数分别为当前行的值，当前行数据，行索引，@return里面可以设置表格<a href=#demo-colspan-rowspan>行/列合并</a></td> <td>Function(text, record, index) {}</td> <td>-</td> </tr> <tr> <td>filters</td> <td>表头的筛选菜单项</td> <td>Array</td> <td>-</td> </tr> <tr> <td>onFilter</td> <td>本地模式下，确定筛选的运行函数</td> <td>Function</td> <td>-</td> </tr> <tr> <td>filterMultiple</td> <td>是否多选</td> <td>Boolean</td> <td>true</td> </tr> <tr> <td>filterDropdown</td> <td>可以自定义筛选菜单，此函数只负责渲染图层，需要自行编写各种交互</td> <td>React.Element</td> <td>-</td> </tr> <tr> <td>sorter</td> <td>排序函数，本地排序使用一个函数，需要服务端排序可设为 true</td> <td>Function or Boolean</td> <td>-</td> </tr> <tr> <td>colSpan</td> <td>表头列合并,设置为 0 时，不渲染</td> <td>Number</td> <td></td> </tr> <tr> <td>width</td> <td>列宽度</td> <td>String or Number</td> <td>-</td> </tr> <tr> <td>className</td> <td>列的 className</td> <td>String</td> <td>-</td> </tr> <tr> <td>fixed</td> <td>列是否固定，可选 <code>true</code>(等效于 left) <code>'left'</code> <code>'right'</code></td> <td>Boolean or String</td> <td>false</td> </tr> <tr> <td>filteredValue</td> <td>筛选的受控属性，外界可用此控制列的筛选状态，值为已筛选的 value 数组</td> <td>Array</td> <td>-</td> </tr> <tr> <td>sortOrder</td> <td>排序的受控属性，外界可用此控制列的排序，可设置为 <code>'ascend'</code> <code>'descend'</code> <code>false</code></td> <td>Boolean or String</td> <td>-</td> </tr> </tbody> </table> <h3>rowSelection</h3> <p>选择功能的配置。</p> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>type</td> <td>多选/单选，<code>checkbox</code> or <code>radio</code></td> <td>String</td> <td><code>checkbox</code></td> </tr> <tr> <td>selectedRowKeys</td> <td>指定选中项的 key 数组，需要和 onChange 进行配合</td> <td>Array</td> <td>[]</td> </tr> <tr> <td>onChange</td> <td>选中项发生变化的时的回调</td> <td>Function(selectedRowKeys, selectedRows)</td> <td>-</td> </tr> <tr> <td>getCheckboxProps</td> <td>选择框的默认属性配置</td> <td>Function(record)</td> <td>-</td> </tr> <tr> <td>onSelect</td> <td>用户手动选择/取消选择某列的回调</td> <td>Function(record, selected, selectedRows)</td> <td>-</td> </tr> <tr> <td>onSelectAll</td> <td>用户手动选择/取消选择所有列的回调</td> <td>Function(selected, selectedRows, changeRows)</td> <td>-</td> </tr> </tbody> </table> <h2>注意</h2> <p>按照 React 的<a href=http://facebook.github.io/react/docs/multiple-components.html#dynamic-children>规范</a>，所有的组件数组必须绑定 key。在 Table 中，<code>dataSource</code> 和 <code>columns</code> 里的数据值都需要指定 <code>key</code> 值。对于 <code>dataSource</code> 默认将每列数据的 <code>key</code> 属性作为唯一的标识。</p> <p>如果你的数据没有这个属性，务必使用 <code>rowKey</code> 来指定数据列的主键。若没有指定，控制台会出现以下的提示，表格组件也会出现各类奇怪的错误。</p> <p><img src=https://os.alipayobjects.com/rmsportal/luLdLvhPOiRpyss.png alt=\"\"></p> <pre><code class=language-jsx><span class=hljs-comment>// 比如你的数据主键是 uid</span>\n<span class=hljs-keyword>return</span> &lt;Table rowKey=\"uid\" /&gt;;\n// 或\nreturn &lt;Table rowKey={record =&gt; record.uid} /&gt;;\n</code></pre> </section>";


