<template><section><h2>Message 全局提示</h2>
<p>全局展示操作反馈信息。</p>
<h2>何时使用</h2>
<ul>
<li>可提供成功、警告和错误等反馈信息。</li>
<li>顶部居中显示并自动消失，是一种不打断用户操作的轻量级提示方式。</li>
</ul>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<ul>
<li><code>Message.success(content, duration)</code></li>
<li><code>Message.error(content, duration)</code></li>
<li><code>Message.info(content, duration)</code></li>
<li><code>Message.warning(content, duration)</code></li>
<li><code>Message.warn(content, duration)</code></li>
<li><code>Message.loading(content, duration)</code></li>
</ul>
<p>组件提供了四个静态方法，参数如下：</p>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>content</td>
<td>提示内容</td>
<td>React.Element or String</td>
<td>-</td>
</tr>
<tr>
<td>duration</td>
<td>自动关闭的延时，单位秒</td>
<td>number</td>
<td>1.5</td>
</tr>
</tbody>
</table>
<p>还提供了全局配置和全局销毁方法：</p>
<ul>
<li><code>Message.config(options)</code></li>
<li><code>Message.destroy()</code></li>
</ul>
<pre><code class="language-js">Message.config({
  top: <span class="hljs-number">100</span>,
  duration: <span class="hljs-number">2</span>,
});
</code></pre>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>top</td>
<td>消息距离顶部的位置</td>
<td>Number</td>
<td>24px</td>
</tr>
<tr>
<td>duration</td>
<td>默认自动关闭延时，单位秒</td>
<td>Number</td>
<td>1.5</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import Demo from 'pages/message/demo'

export default {
  components: {
    Demo
  }
}
</script>


