<template><section><h2>Icon 图标</h2>
<p>有含义的矢量图形，每一个图标打倒一个敌人。</p>
<h2>图标的命名规范</h2>
<p>我们为每个图标赋予了语义化的命名，命名规则如下:</p>
<ul>
<li>
<p>实心和描线图标保持同名，用 <code>-o</code> 来区分，比如 <code>question-circle</code>(实心) 和 <code>question-circle-o</code>(描线)；</p>
</li>
<li>
<p>命名顺序：<code>[icon名]-[形状可选]-[描线与否]-[方向可选]</code>。</p>
</li>
</ul>
<h2>如何使用</h2>
<p>使用 <code>&lt;ant-icon&gt;&lt;/ant-icon&gt;</code> 标签声明组件，指定图标对应的 type 属性，示例代码如下:</p>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">ant-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"link"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">ant-icon</span>&gt;</span>
</code></pre>
<p>最终会渲染为：</p>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"anticon anticon-${type}"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
</code></pre>
<h2>本地部署</h2>
<p>图标组件使用 <a href="http://iconfont.cn">iconfont.cn</a>，默认公网可访问。如需本地部署，可参考 <a href="https://github.com/ant-design/antd-init/tree/master/examples/local-iconfont">示例</a>。</p>
<h2>图标列表</h2>
<blockquote>
<p>点击图标复制代码。</p>
</blockquote>
<h3>一. 方向性图标</h3>
<ul class="anticons-list">
  <copyable-icon v-for="icon in icons" :type="icon"></copyable-icon>
</ul>
<h3>二. 提示建议性图标</h3>
<ul class="anticons-list">
  <copyable-icon v-for="icon in icons2" :type="icon"></copyable-icon>
</ul>
<h3>三. 网站通用图标</h3>
<ul class="anticons-list">
  <copyable-icon v-for="icon in icons3" :type="icon"></copyable-icon>
</ul>


</section></template>
<style scoped>
.markdown .icons {
  width: 100%;
}
ul.anticons-list {
  margin: 20px 0;
  list-style: none;
  overflow: hidden;
}
