<template><section><h2>Alert 警告提示</h2>
<p>警告提示，展现需要关注的信息。</p>
<h2>何时使用</h2>
<ul>
<li>当某个页面需要向用户显示警告的信息时。</li>
<li>非浮层的静态展现形式，始终展现，不会自动消失，用户可以点击关闭。</li>
</ul>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>指定警告提示的样式，有四种选择 <code>success</code>、<code>info</code>、<code>warning</code>、<code>error</code></td>
<td>String</td>
<td><code>info</code></td>
</tr>
<tr>
<td>closable</td>
<td>默认不显示关闭按钮</td>
<td>Boolean</td>
<td>无</td>
</tr>
<tr>
<td>closeText</td>
<td>自定义关闭按钮</td>
<td>React.Node</td>
<td>无</td>
</tr>
<tr>
<td>message</td>
<td>警告提示内容</td>
<td>React.Node</td>
<td>无</td>
</tr>
<tr>
<td>description</td>
<td>警告提示的辅助性文字介绍</td>
<td>React.Node</td>
<td>无</td>
</tr>
<tr>
<td>onClose</td>
<td>关闭时触发的回调函数</td>
<td>Function</td>
<td>无</td>
</tr>
<tr>
<td>showIcon</td>
<td>是否显示辅助图标</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>banner</td>
<td>是否用作顶部公告</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
</section></template>

