<template><section><h2>Affix 固钉</h2>
<p>将页面元素钉在可视范围。</p>
<h2>何时使用</h2>
<p>当内容区域比较长，需要滚动页面时，这部分内容对应的操作或者导航需要在滚动范围内始终展现。常用于侧边菜单和按钮组合。</p>
<p>页面可视范围过小时，慎用此功能以免遮挡页面内容。</p>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<table>
<thead>
<tr>
<th>成员</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>offsetTop</td>
<td>距离窗口顶部达到指定偏移量后触发</td>
<td>Number</td>
<td></td>
</tr>
<tr>
<td>offsetBottom</td>
<td>距离窗口底部达到指定偏移量后触发</td>
<td>Number</td>
<td></td>
</tr>
<tr>
<td>target</td>
<td>设置 <code>Affix</code> 需要监听其滚动事件的元素，值为一个返回对应 DOM 元素的函数</td>
<td>Function</td>
<td>() =&gt; window</td>
</tr>
<tr>
<td>onChange</td>
<td>固定状态改变时触发的回调函数</td>
<td>Function(affixed)</td>
<td>无</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import Demo from 'pages/affix/demo'

export default {
  components: {
    Demo
  }
}
</script>


