<template><section><h2>Radio 单选框</h2>
<p>单选框。</p>
<h2>何时使用</h2>
<ul>
<li>用于在多个备选项中选中单个状态。</li>
<li>和 Select 的区别是，Radio 所有选项默认可见，方便用户在比较中选择，因此选项不宜过多。</li>
</ul>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<h3>Radio</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>checked</td>
<td>指定当前是否选中</td>
<td>Boolean</td>
<td></td>
<td>false</td>
</tr>
<tr>
<td>defaultChecked</td>
<td>初始是否选中</td>
<td>Boolean</td>
<td></td>
<td>false</td>
</tr>
<tr>
<td>value</td>
<td>根据 value 进行比较，判断是否选中</td>
<td>String</td>
<td></td>
<td>无</td>
</tr>
</tbody>
</table>
<h3>RadioGroup</h3>
<p>单选框组合，用于包裹一组 <code>Radio</code>。</p>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>onChange</td>
<td>选项变化时的回调函数</td>
<td>Function(e:Event)</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>value</td>
<td>用于设置当前选中的值</td>
<td>String</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>defaultValue</td>
<td>默认选中的值</td>
<td>String</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>size</td>
<td>大小，只对按钮样式生效</td>
<td>String</td>
<td><code>large</code> <code>default</code> <code>small</code></td>
<td><code>default</code></td>
</tr>
</tbody>
</table>
</section></template>

<script>
import Demo from 'pages/radio/demo'

export default {
  components: {
    Demo
  }
}
</script>


