<template><section><h2>Notification 通知提醒框</h2>
<p>全局展示通知提醒信息。</p>
<h2>何时使用</h2>
<p>在系统右上角显示通知提醒信息。经常用于以下情况：</p>
<ul>
<li>较为复杂的通知内容。</li>
<li>带有交互的通知，给出用户下一步的行动点。</li>
<li>系统主动推送。</li>
</ul>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<ul>
<li><code>Notification.success(config)</code></li>
<li><code>Notification.error(config)</code></li>
<li><code>Notification.info(config)</code></li>
<li><code>Notification.warning(config)</code></li>
<li><code>Notification.warn(config)</code></li>
<li><code>Notification.close(key: String)</code></li>
<li><code>Notification.destroy()</code></li>
</ul>
<p>config 参数如下：</p>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>message</td>
<td>通知提醒标题，必选</td>
<td>React.Element or String</td>
<td>无</td>
</tr>
<tr>
<td>description</td>
<td>通知提醒内容，必选</td>
<td>React.Element or String</td>
<td>无</td>
</tr>
<tr>
<td>btn</td>
<td>自定义关闭按钮</td>
<td>React.Element</td>
<td>无</td>
</tr>
<tr>
<td>key</td>
<td>当前通知唯一标志</td>
<td>String</td>
<td>无</td>
</tr>
<tr>
<td>onClose</td>
<td>点击默认关闭按钮时触发的回调函数</td>
<td>Function</td>
<td>无</td>
</tr>
<tr>
<td>duration</td>
<td>默认 4.5 秒后自动关闭，配置为 null 则不自动关闭</td>
<td>Number</td>
<td>4.5</td>
</tr>
</tbody>
</table>
<p>还提供了一个全局配置方法，在调用前提前配置，全局一次生效。</p>
<ul>
<li><code>Notification.config(options)</code></li>
</ul>
<pre><code class="language-js">Notification.config({
  top: <span class="hljs-number">100</span>,
  duration: <span class="hljs-number">3</span>,
});
</code></pre>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>top</td>
<td>消息距离顶部的位置</td>
<td>Number</td>
<td>24px</td>
</tr>
<tr>
<td>duration</td>
<td>默认自动关闭延时，单位秒</td>
<td>Number</td>
<td>4.5</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import Demo from 'pages/notification/demo'

export default {
  components: {
    Demo
  }
}
</script>


