<template><section><h2>Timeline 时间轴</h2>
<p>垂直展示的时间流信息。</p>
<h2>何时使用</h2>
<ul>
<li>当有一系列信息需要从上至下按时间排列时；</li>
<li>需要有一条时间轴进行视觉上的串联时；</li>
</ul>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<pre><code class="language-jsx">&lt;ant-timeline&gt;
  &lt;ant-timeline-item&gt;创建服务现场 2015-09-01&lt;/ant-timeline-item&gt;
  &lt;ant-timeline-item&gt;初步排除网络异常 2015-09-01&lt;/ant-timeline-item&gt;
  &lt;ant-timeline-item&gt;技术测试异常 2015-09-01&lt;/ant-timeline-item&gt;
  &lt;ant-timeline-item&gt;网络异常正在修复 2015-09-01&lt;/ant-timeline-item&gt;
&lt;/ant-timeline&gt;
</code></pre>
<h3>Timeline</h3>
<p>时间轴。</p>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>pending</td>
<td>指定最后一个幽灵节点是否存在或内容</td>
<td>boolean or React.Element</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>TimelineItem</h3>
<p>时间轴的每一个节点。</p>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>color</td>
<td>指定圆圈颜色 <code>blue, red, green</code>，或自定义的色值</td>
<td>string</td>
<td>blue</td>
</tr>
<tr>
<td>dot</td>
<td>自定义时间轴点</td>
<td>React.Element</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

