<template><section><h2>Progress 进度条</h2>
<p>展示操作的当前进度。</p>
<h2>何时使用</h2>
<p>在操作需要较长时间才能完成时，为用户显示该操作的当前进度和状态。</p>
<ul>
<li>当一个操作会打断当前界面，或者需要在后台运行，且耗时可能超过2秒时；</li>
<li>当需要显示一个操作完成的百分比时。</li>
</ul>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>类型，可选 <code>line</code> <code>circle</code></td>
<td>String</td>
<td>line</td>
</tr>
<tr>
<td>percent</td>
<td>百分比</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>format</td>
<td>内容的模板函数</td>
<td>function(percent)</td>
<td><code>percent =&gt; percent + '%'</code></td>
</tr>
<tr>
<td>status</td>
<td>状态，可选：<code>success</code> <code>exception</code> <code>active</code></td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>showInfo</td>
<td>是否显示进度数值或状态图标</td>
<td>Boolean</td>
<td>true</td>
</tr>
<tr>
<td>strokeWidth <code>(type=line)</code></td>
<td>进度条线的宽度，单位 px</td>
<td>Number</td>
<td>10</td>
</tr>
<tr>
<td>strokeWidth <code>(type=circle)</code></td>
<td>圆形进度条线的宽度，单位是进度条画布宽度的百分比</td>
<td>Number</td>
<td>6</td>
</tr>
<tr>
<td>width <code>(type=circle)</code></td>
<td>圆形进度条画布宽度，单位 px</td>
<td>Number</td>
<td>132</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import Demo from 'pages/progress/demo'

export default {
  components: {
    Demo
  }
}
</script>


