<template><section><h2>Steps 步骤条</h2>
<p>引导用户按照流程完成任务的导航条。</p>
<h2>何时使用</h2>
<p>当任务复杂或者存在先后关系时，将其分解成一系列步骤，从而简化任务。</p>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<pre><code class="language-jsx">&lt;ant-steps&gt;
  &lt;ant-step title="第一步"&gt;&lt;/ant-step&gt;
  &lt;ant-step title="第二步"&gt;&lt;/ant-step&gt;
  &lt;ant-step title="第三步"&gt;&lt;/ant-step&gt;
&lt;/ant-steps&gt;
</code></pre>
<h3>Steps</h3>
<p>整体步骤条。</p>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>current</td>
<td>指定当前步骤，从 0 开始记数。在子 Step 元素中，可以通过 <code>status</code> 属性覆盖状态</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>status</td>
<td>指定当前步骤的状态，可选 <code>wait</code> <code>process</code> <code>finish</code> <code>error</code></td>
<td>String</td>
<td>process</td>
</tr>
<tr>
<td>size</td>
<td>指定大小，目前支持普通（<code>default</code>）和迷你（<code>small</code>）</td>
<td>String</td>
<td>default</td>
</tr>
<tr>
<td>direction</td>
<td>指定步骤条方向。目前支持水平（<code>horizontal</code>）和竖直（<code>vertical</code>）两种方向</td>
<td>String</td>
<td>horizontal</td>
</tr>
</tbody>
</table>
<h3>Step</h3>
<p>步骤条内的每一个步骤。</p>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>status</td>
<td>指定状态。当不配置该属性时，会使用 Steps 的 <code>current</code> 来自动指定状态。可选：<code>wait</code> <code>process</code> <code>finish</code> <code>error</code></td>
<td>String</td>
<td>wait</td>
</tr>
<tr>
<td>title</td>
<td>标题</td>
<td>React.Element</td>
<td>-</td>
</tr>
<tr>
<td>description</td>
<td>步骤的详情描述，可选</td>
<td>React.Element</td>
<td>-</td>
</tr>
<tr>
<td>icon</td>
<td>步骤图标，可选</td>
<td>React.Element</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import Demo from 'pages/steps/demo'

export default {
  components: {
    Demo
  }
}
</script>


