<template><section><h2>Tooltip 文字提示</h2>
<p>简单的文字提示气泡框。</p>
<h2>何时使用</h2>
<p>鼠标移入则显示提示，移出消失，气泡浮层不承载复杂文本和操作。</p>
<p>可用来代替系统默认的 <code>title</code> 提示，提供一个<code>按钮/文字/操作</code>的文案解释。</p>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>placement</td>
<td>气泡框位置，可选 <code>top</code> <code>left</code> <code>right</code> <code>bottom</code> <code>topLeft</code> <code>topRight</code> <code>bottomLeft</code> <code>bottomRight</code> <code>leftTop</code> <code>leftBottom</code> <code>rightTop</code> <code>rightBottom</code></td>
<td>string</td>
<td>top</td>
</tr>
<tr>
<td>title</td>
<td>提示文字</td>
<td>string/React.Element</td>
<td>无</td>
</tr>
<tr>
<td>getTooltipContainer</td>
<td>浮层渲染父节点。默认渲染到 body 上</td>
<td>Function(triggerNode)</td>
<td>() =&gt; document.body</td>
</tr>
</tbody>
</table>
<p>更多 API 可参考：https://github.com/react-component/tooltip</p>
</section></template>

<script>
import Demo from 'pages/tooltip/demo'

export default {
  components: {
    Demo
  }
}
</script>


