module.exports = "<section><h2>Pagination 分页</h2> <p>采用分页的形式分隔长列表，每次只加载一个页面。</p> <h2>何时使用</h2> <ul> <li>当加载/渲染所有数据将花费很多时间时；</li> <li>可切换页码浏览数据。</li> </ul> <h2>代码演示</h2> <p><demo></demo></p> <h2>API</h2> <pre><code class=language-html><span class=hljs-tag>&lt;<span class=hljs-name>ant-pagination</span> <span class=hljs-attr>:on-change</span>=<span class=hljs-string>\"onChange\"</span> <span class=hljs-attr>:total</span>=<span class=hljs-string>\"50\"</span>&gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>ant-pagination</span>&gt;</span>\n</code></pre> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>current</td> <td>当前页数</td> <td>Number</td> <td>-</td> </tr> <tr> <td>defaultCurrent</td> <td>默认的当前页数</td> <td>Number</td> <td>1</td> </tr> <tr> <td>total</td> <td>数据总数</td> <td>Number</td> <td>0</td> </tr> <tr> <td>defaultPageSize</td> <td>默认的每页条数</td> <td>Number</td> <td>10</td> </tr> <tr> <td>pageSize</td> <td>每页条数</td> <td>Number</td> <td>-</td> </tr> <tr> <td>onChange</td> <td>页码改变的回调，参数是改变后的页码</td> <td>Function</td> <td>noop</td> </tr> <tr> <td>showSizeChanger</td> <td>是否可以改变 pageSize</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>pageSizeOptions</td> <td>指定每页可以显示多少条</td> <td>Array&lt;String&gt;</td> <td>['10', '20', '30', '40']</td> </tr> <tr> <td>onShowSizeChange</td> <td>pageSize 变化的回调</td> <td>Function</td> <td>noop</td> </tr> <tr> <td>showQuickJumper</td> <td>是否可以快速跳转至某页</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>size</td> <td>当为「small」时，是小尺寸分页</td> <td>String</td> <td>&quot;&quot;</td> </tr> <tr> <td>simple</td> <td>当添加该属性时，显示为简单分页</td> <td>Object</td> <td>-</td> </tr> <tr> <td>showTotal</td> <td>用于显示总共有多少条数据</td> <td>Function</td> <td>-</td> </tr> </tbody> </table> </section>";


