<template><section><h2>Switch 开关</h2>
<p>开关选择器。</p>
<h2>何时使用</h2>
<ul>
<li>需要表示开关状态/两种状态之间的切换时；</li>
<li>和 <code>checkbox</code>的区别是，切换 <code>switch</code> 会直接触发状态改变，而 <code>checkbox</code> 一般用于状态标记，需要和提交操作配合。</li>
</ul>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<h3>Switch</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>checked</td>
<td>指定当前是否选中</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>defaultChecked</td>
<td>初始是否选中</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>onChange</td>
<td>变化时回调函数</td>
<td>Function(checked:Boolean)</td>
<td></td>
</tr>
<tr>
<td>checkedChildren</td>
<td>选中时的内容</td>
<td>React Node</td>
<td></td>
</tr>
<tr>
<td>unCheckedChildren</td>
<td>非选中时的内容</td>
<td>React Node</td>
<td></td>
</tr>
<tr>
<td>size</td>
<td>开关大小，可选值：<code>default</code> <code>small</code></td>
<td>String</td>
<td>default</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import Demo from 'pages/switch/demo'

export default {
  components: {
    Demo
  }
}
</script>


