<template><section><h2>Layout 栅格</h2>
<p>24 栅格系统。</p>
<h2>设计理念</h2>
<div class="layout-demo">
<div class="ant-row demo-row">
  <div class="ant-col-24 demo-col demo-col-1">
    100%
  </div>
</div>
<div class="ant-row demo-row">
