<h2>API</h2>
<h3>Row</h3>
<table>
<thead>
<tr>
<th>成员</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>gutter</td>
<td>栅格间隔</td>
<td>number</td>
<td>0</td>
</tr>
<tr>
<td>type</td>
<td>布局模式，可选 <code>flex</code>，现代浏览器下有效</td>
<td>string</td>
<td></td>
</tr>
<tr>
<td>align</td>
<td>flex 布局下的垂直对齐方式：<code>top</code> <code>middle</code> <code>bottom</code></td>
<td>string</td>
<td><code>top</code></td>
</tr>
<tr>
<td>justify</td>
<td>flex 布局下的水平排列方式：<code>start</code> <code>end</code> <code>center</code> <code>space-around</code> <code>space-between</code></td>
<td>string</td>
<td><code>start</code></td>
</tr>
</tbody>
</table>
<h3>Col</h3>
<table>
<thead>
<tr>
<th>成员</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>span</td>
<td>栅格占位格数，为 0 时相当于 <code>display: none</code></td>
<td>number</td>
<td>-</td>
</tr>
<tr>
<td>order</td>
<td>栅格顺序，<code>flex</code> 布局模式下有效</td>
<td>number</td>
<td>0</td>
</tr>
<tr>
<td>offset</td>
<td>栅格左侧的间隔格数，间隔内不可以有栅格</td>
<td>number</td>
<td>0</td>
</tr>
<tr>
<td>push</td>
<td>栅格向右移动格数</td>
<td>number</td>
<td>0</td>
</tr>
<tr>
<td>pull</td>
