<td><code>&lt;768px</code> 响应式栅格，可为栅格数或一个包含其他属性的对象</td>
<td>number or object</td>
<td>-</td>
</tr>
<tr>
<td>sm</td>
<td><code>≥768px</code> 响应式栅格，可为栅格数或一个包含其他属性的对象</td>
<td>number or object</td>
<td>-</td>
</tr>
<tr>
<td>md</td>
