<td>number or object</td>
<td>-</td>
</tr>
<tr>
<td>lg</td>
<td><code>≥1200px</code> 响应式栅格，可为栅格数或一个包含其他属性的对象</td>
<td>number or object</td>
<td>-</td>
</tr>
</tbody>
</table>
