module.exports = "<section><h2>Modal 对话框</h2> <p>模态对话框。</p> <h2>何时使用</h2> <p>需要用户处理事务，又不希望跳转页面以致打断工作流程时，可以使用 <code>Modal</code> 在当前页面正中打开一个浮层，承载相应的操作。</p> <p>另外当需要一个简洁的确认框询问用户时，可以使用精心封装好的 <code>ant.Modal.confirm()</code> 等方法。</p> <h2>代码演示</h2> <p><demo></demo></p> <h2>API</h2> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>visible</td> <td>对话框是否可见</td> <td>Boolean</td> <td>无</td> </tr> <tr> <td>confirmLoading</td> <td>确定按钮 loading</td> <td>Boolean</td> <td>无</td> </tr> <tr> <td>title</td> <td>标题</td> <td>React.Element</td> <td>无</td> </tr> <tr> <td>closable</td> <td>是否显示右上角的关闭按钮</td> <td>Boolean</td> <td>true</td> </tr> <tr> <td>onOk</td> <td>点击确定回调</td> <td>function</td> <td>无</td> </tr> <tr> <td>onCancel</td> <td>点击遮罩层或右上角叉或取消按钮的回调</td> <td>function(e)</td> <td>无</td> </tr> <tr> <td>width</td> <td>宽度</td> <td>String or Number</td> <td>520</td> </tr> <tr> <td>footer</td> <td>底部内容</td> <td>React.Element</td> <td>确定取消按钮</td> </tr> <tr> <td>okText</td> <td>确认按钮文字</td> <td>String</td> <td>确定</td> </tr> <tr> <td>cancelText</td> <td>取消按钮文字</td> <td>String</td> <td>取消</td> </tr> <tr> <td>maskClosable</td> <td>点击蒙层是否允许关闭</td> <td>Boolean</td> <td>true</td> </tr> <tr> <td>style</td> <td>可用于设置浮层的样式，调整浮层位置等</td> <td>Object</td> <td>-</td> </tr> <tr> <td>wrapClassName</td> <td>对话框外层容器的类名</td> <td>String</td> <td>-</td> </tr> </tbody> </table> <h3>Modal.xxx()</h3> <p>包括：</p> <ul> <li><code>Modal.info</code></li> <li><code>Modal.success</code></li> <li><code>Modal.error</code></li> <li><code>Modal.warning</code></li> <li><code>Modal.confirm</code></li> </ul> <p>以上均为一个函数，参数为 object，具体属性如下：</p> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>title</td> <td>标题</td> <td>React.Element or String</td> <td>无</td> </tr> <tr> <td>content</td> <td>内容</td> <td>React.Element or String</td> <td>无</td> </tr> <tr> <td>onOk</td> <td>点击确定回调，参数为关闭函数，返回 promise 时 resolve 后自动关闭</td> <td>function</td> <td>无</td> </tr> <tr> <td>onCancel</td> <td>取消回调，参数为关闭函数，返回 promise 时 resolve 后自动关闭</td> <td>function</td> <td>无</td> </tr> <tr> <td>width</td> <td>宽度</td> <td>String or Number</td> <td>416</td> </tr> <tr> <td>iconType</td> <td>图标 Icon 类型</td> <td>String</td> <td>question-circle</td> </tr> <tr> <td>okText</td> <td>确认按钮文字</td> <td>String</td> <td>确定</td> </tr> <tr> <td>cancelText</td> <td>取消按钮文字</td> <td>String</td> <td>取消</td> </tr> </tbody> </table> </section>";


