<template><section><h2>InputNumber 数字输入框</h2>
<p>通过鼠标或键盘，输入范围内的数值。</p>
<h2>何时使用</h2>
<p>当需要获取标准数值时。</p>
<h2>代码演示</h2>
<p><demo></demo></p>

<h2>API</h2>
<p>属性如下</p>
<table>
<thead>
<tr>
<th>成员</th>
<th>说明</th>
<th>类型</th>
