<template lang="html">
  <div>
    <code-box id="components-modal-demo-footer" title="自定义页脚" :code="code" :description="description">
      <div slot="instance">
        <ant-button type="primary" :on-click="showModal">显示对话框</ant-button>
        <ant-modal ref="modal"
          :visible="visible"
          title="对话框标题"
          :on-ok="handleOk"
          :on-cancel="handleCancel"
        >
          <div slot="footer">
            <ant-button key="back" type="ghost" size="large" :on-click="handleCancel">返 回</ant-button>
            <ant-button key="submit" type="primary" size="large" :loading="loading" :on-click="handleOk">
              提 交
            </ant-button>
          </div>
          <p>对话框的内容</p>
          <p>对话框的内容</p>
          <p>对话框的内容</p>
          <p>对话框的内容</p>
          <p>对话框的内容</p>
        </ant-modal>
      </div>
    </code-box>
  </div>
</template>

<script>
import { template as description } from './basic.md'

export default {
  data: () => ({
    loading: false,
    visible: false,
    description,
    code: `
      <template lang="html">
        <div>
          <ant-button type="primary" :on-click="showModal">显示对话框</ant-button>
          <ant-modal title="第一个 Modal" :visible="visible"
            :on-ok="handleOk" :on-cancel="handleCancel"
          >
            <p>对话框的内容</p>
            <p>对话框的内容</p>
            <p>对话框的内容</p>
          </ant-modal>
        </div>
      </template>

      \<script\>
      export default {
        data: () => ({
          visible: false
        }),
        methods: {
          showModal () {
            this.visible = true
          },
          handleOk () {
            console.log('点击了确定')
            this.visible = false
          },
          handleCancel (e) {
            console.log(e)
            this.visible = false
          }
        }
      }
      \</script\>
    `
  }),
  methods: {
    showModal () {
      this.visible = true
    },
    handleOk () {
      this.loading = true
      setTimeout(() => {
        this.loading = false
        this.visible = false
      }, 3000)
    },
    handleCancel () {
      this.visible = false
    }
  }
}
</script>



